import { AuthInput } from "components/AuthInput";
import { ReactComponent as LogoSVG } from "assets/Icon.svg";
import { Link } from "react-router-dom";
import styles from "pages/LoginRegister.module.scss";
import { register } from "api/auth";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { AlertModal } from "components/modals/AlertModal";
import Swal from "sweetalert2";
import clsx from "clsx";

export const Register = () => {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const [alertWord, setAlertWord] = useState("");
  const [namePassed, setNamePassed] = useState(true);
  const [accountPassed, setAccountPassed] = useState(true);
  const [pwdPassed, setPwdPassed] = useState(true);
  const [emailPassed, setEmailPassed] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    if (showAlert) {
      const timeout = setTimeout(() => {
        setShowAlert(false);
        setAlertWord("");
      }, 3000);
      return () => clearTimeout(timeout); // cleanup function
    }
  }, [showAlert]);

  const handleRegister = async () => {
    if (account.length === 0) return;
    if (name.length === 0) return;
    if (email.length === 0) return;
    if (password.length === 0) return;
    if (checkPassword.length === 0) return;

    const res = await register({
      account,
      name,
      email,
      password,
      checkPassword,
    });
    console.log(`success: ${res.success} message:${res.message} register`);
    switch (res.message) {
      case "Create success":
        Swal.fire({
          title: "!",
          icon: "success",
          iconColor: "#82C43C",
          showConfirmButton: false,
          timer: 1000,
          position: "top",
        });
        navigate("/login");
        break;
      case "Max length 50":
        setShowAlert(true);
        setNamePassed(false);
        setAlertWord("50");
        break;
      case "Email already exists!":
        setShowAlert(true);
        setEmailPassed(false);
        setAlertWord("Email ");
        break;
      case "Account already exists!":
        setShowAlert(true);
        setAccountPassed(false);
        setAlertWord("");
        break;
      case "Password do not match!":
        setShowAlert(true);
        setPwdPassed(false);
        setAlertWord("");
        break;
      default:
        Swal.fire({
          title: "!",
          icon: "error",
          showConfirmButton: false,
          timer: 1000,
          position: "top",
        });
        break;
    }
  };

  return (
    <div className={styles.authContainer}>
      <div>
        <LogoSVG />
      </div>
      <h1></h1>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={account}
          onChange={(e) => {
            setAccount(e.target.value);
            setAccountPassed(true);
          }}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
          accountPassed={accountPassed}
        />
        <span
          className={clsx("", {
            [styles.limit]: !accountPassed,
            [styles.noLimit]: accountPassed,
          })}
        >
          
        </span>
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={name}
          onChange={(e) => {
            setName(e.target.value);
            setNamePassed(true);
          }}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
          namePassed={namePassed}
        />
        <span
          className={clsx("", {
            [styles.limit]: !namePassed,
            [styles.noLimit]: namePassed,
          })}
        >
          
        </span>
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="text"
          value={email}
          onChange={(e) => {
            setEmail(e.target.value);
            setEmailPassed(true);
          }}
          label="Email"
          placeholder="Email"
          onKeyDown={handleRegister}
          emailPassed={emailPassed}
        />
        <span
          className={clsx("", {
            [styles.limit]: !emailPassed,
            [styles.noLimit]: emailPassed,
          })}
        >
          Email 
        </span>
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="password"
          value={password}
          onChange={(e) => {
            setPassword(e.target.value);
            setPwdPassed(true);
          }}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
          pwdPassed={pwdPassed}
        />
        <span
          className={clsx("", {
            [styles.limit]: !pwdPassed,
            [styles.noLimit]: pwdPassed,
          })}
        >
          
        </span>
      </div>
      <div className={styles.inputContainer}>
        <AuthInput
          type="password"
          value={checkPassword}
          onChange={(e) => {
            setCheckPassword(e.target.value);
            setPwdPassed(true);
          }}
          label=""
          placeholder=""
          onKeyDown={handleRegister}
          pwdPassed={pwdPassed}
        />
        <span
          className={clsx("", {
            [styles.limit]: !pwdPassed,
            [styles.noLimit]: pwdPassed,
          })}
        >
          
        </span>
      </div>

      <button
        data-bs-toggle="modal"
        data-bs-target="#alertModal"
        className={styles.loginButton}
        onClick={handleRegister}
      >
        
      </button>
      {showAlert && <AlertModal value={alertWord} />}
      <div className={styles.linkTextCancel}>
        <Link to="/login"></Link>
      </div>
    </div>
  );
};
